/* blinker-200 - latin */
@font-face {
    font-family: 'Blinker';
    font-style: normal;
    font-weight: 200;
    src: local('Blinker ExtraLight'), local('Blinker-ExtraLight'),
         url('/fonts/blinker-v3-latin-200.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
         url('/fonts/blinker-v3-latin-200.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  }
  /* blinker-regular - latin */
  @font-face {
    font-family: 'Blinker';
    font-style: normal;
    font-weight: 400;
    src: local('Blinker'), local('Blinker-Regular'),
         url('/fonts/blinker-v3-latin-regular.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
         url('/fonts/blinker-v3-latin-regular.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  }
  /* blinker-700 - latin */
  @font-face {
    font-family: 'Blinker';
    font-style: normal;
    font-weight: 700;
    src: local('Blinker Bold'), local('Blinker-Bold'),
         url('/fonts/blinker-v3-latin-700.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
         url('/fonts/blinker-v3-latin-700.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  }